
/**
 * Provides access to sound effects.
 * @class sounds
 * @module services
 */
angular.module("lcars").service("lcSounds", function() {
	var service = this;
	var sounds = {};
	var active = true;

	var Sound = function(data, format) {
		format = format || "mp3";
		var snd = new Audio("data:audio/" + format + ";base64," + data);

		this.play = function() {
			if(active) {
				snd.play();
			}
		};
	};

	var init = function() {
		Object.keys(sounds).forEach(function(key) {
			sounds[key] = new Sound(sounds[key]);
			service[key] = sounds[key].play;
		});
	};

	this.play = function(key) {
		if(sounds[key]) {
			sounds[key].play();
		} else {
			console.warn("No sound index for " + key);
		}
	};

	this.mute = function(mute) {
		active = !mute;
	};

	this.muted = function() {
		return !active;
	};

	sounds.notification = "";
	sounds.powerdown = "";
	sounds.critical = "";
	sounds.powerup = "";
	sounds.warning = "";
	sounds.offline = "";
	sounds.online = "";
	sounds.alert = "";
	sounds.chirp = "";
	sounds.beep = "";
	sounds.scan = "";
	sounds.busy = "";
	sounds.error = "";
	sounds.button = "";
	sounds.close =  "";
	sounds.open =   "";
	init();
});
